.select2-dropdown .select2-results ul.select2-results__options li.select2-results__option {
  height: 48px;
  padding: 15px 20px;
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 110%;
  color: #FFFFFF;
  background-color: transparent;
}

.select2-container .select2-search {
  display: none;
}

.popap {
  position: fixed;
  z-index: 999;
  top: -120%;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease 0s;
}
.popap.active {
  top: 0;
}
.popap__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.5;
}
.popap__container {
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 440px;
  max-width: 100%;
  padding: 40px;
  background: #03010F;
  border-radius: 10px;
}
.popap__container img.circle-back-p {
  transform: translate(-50%, -50%);
  top: 0;
}
.popap__text {
  margin-top: 20px;
}
.popap__subtitle {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 110%;
  color: #FFFFFF;
  margin-top: 40px;
}
.popap__form {
  display: flex;
  flex-direction: column;
}
.popap__input {
  margin-top: 20px;
}
.popap__btn {
  margin-top: 40px;
  margin-left: auto;
}